<template>
<div class="container-fluid">
    <div class="bg-white row align-items-center p-1">
        <div class="col-auto" @click="$tourl('../')">
            <span class="fs-4 bi bi-chevron-left"></span>
        </div>
        <div class="col">
            <div class="pt-1 text-center fs-5">{{title}}</div>
        </div>
        <div class="col-auto">
            <span v-if="data && data.type==='btn'" class="text-black-50 fs-normal" @click="$emit('onselect')">{{data.name}}</span>
        </div>
    </div>
</div>
</template>

<script>
export default {
  props:{
    title:'',
    data:'',
  },
}
</script>


