<template>
  <div>
      <com-layout-title title="正在为您匹配车主" />
      <div class="d-flex justify-content-between align-items-center bg-info bg-gradient text-white fs-normal p-2" v-if="data.show">
        <div class="bd-highlight">
          <div class="fs-normal">{{data.show.setdate|setdate(data.show.setdate_end)}} | {{data.show.state|tripstate}}</div>
          <div>
            <span>{{data.show.origin}}</span>
            <span>到</span>
            <span>{{data.show.destination}}</span>
          </div>
        </div>
        <div class="bd-highlight">
         
        </div>
      </div>
  </div>
</template>

<script>
export default {
  components: { 
  },
  props: {
    set:{},
    data:{},
  },
  data(){
    return {
      isActive:false,
    }
  },
  methods:{
    onselect(){
      this.$toast({type:'select',data:[{name:'重新发布行程',url:'/passenger/cancel',id:this.show.id},{name:'取消当前行程',url:'/passenger/cancel',id:this.show.id}]}).then((e) => {
        this.onaxios({method: 'post',url: e.url,params:{token:this.$store.state.token},data:{id:e.id}});
      });
    },
    onaxios(key){
        this.loading=true;
        key.url=this.$store.state.apiset.url+key.url;
        axios(key).then(response => {
            this.loading=false;
            let res=response.data;
            if(res.code==1){

             }else{
               this.$toast({type:'result',data:res});
             }
          }).catch(function (error) {this.loading=false;console.log(error)})
    }
  }
}
</script>


