<template>
<div class="bg-white">
  <com-layout-title :title="set.title" :data="{type:'btn',name:'取消订单'}" @onselect="onselect"/>
  <div class="d-flex justify-content-between align-items-center bg-info bg-gradient text-white fs-normal  p-2">
    <div class="bd-highlight">
      <div class="fs-normal">正在匹配行程 | 可载{{show.people}}人</div>
      <div><span class="inbefore">{{show.origin}}</span><span class="inbefore">{{show.destination}}</span></div>
    </div>
    <div class="bd-highlight">
      <span class="fs-3 bi bi-broadcast"></span>
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  components: { 
  },
  props: {
    set:{},
    data:{},
  },
  data(){
      return {
          show:{},
          loading:false,
      }
  },
  created(){
      this.show=this.data.show;
  },
  methods:{
    onselect(){
      this.$toast({type:'select',data:[{name:'重新发布行程',url:'/driver/cancel',id:this.show.id},{name:'取消当前行程',url:'/driver/cancel',id:this.show.id}]}).then((e) => {
        this.onaxios({method: 'post',url: e.url,params:{token:this.$store.state.token},data:{id:e.id}});
      });
    },
    onaxios(key){
        this.loading=true;
        key.url=this.$store.state.apiset.url+key.url;
        axios(key).then(response => {
            this.loading=false;
            let res=response.data;
            if(res.code==1){

             }else{
               this.$toast({type:'result',data:res});
             }
          }).catch(function (error) {this.loading=false;console.log(error)})
    }
  }
}
</script>



